import appTabNav from "../../../components/basic-component/app-tab-nav/app-tab-nav.vue";

import { mapState } from "vuex";

export default {
    data() {
        return {
            theme: {
                color: '#ff4544'
            },
            tabList: [
                {id:-1, name: '全部'},
                {id:0, name: '待审核'},
                {id:1, name: '待打款'},
                {id:2, name: '已打款'},
                {id:3, name: '无效'},
            ],
            loading: null,
            list: [],
            activeTab: -1,
            noBorder: false,
            id: null,
            page: 2
        }
    },
    components: {
        "app-tab-nav": appTabNav,
    },
    computed: {
        ...mapState({
            mall: state => state.mallConfig.mall,
            custom_setting: state => state.mallConfig.share_setting_custom,
            share_setting: state => state.mallConfig.share_setting,
        })
    },
    methods: {
        open(e) {
            this.id = e;
        },

        tabStatus(e) {
            uni.showLoading({
                mask: true,
                title: '加载中...'
            });
            this.list = [];
            this.page = 2;
            this.activeTab = e.currentTarget.dataset.id;
            this.getList();
        },
        getList() {
            let that = this;
            that.$request({
                url: that.$api.bonus.detail,
                data: {
                    status: that.activeTab
                },
            }).then(response=>{
                that.$hideLoading();
                uni.hideLoading();
                if(response.code == 0) {
                    that.list = response.data.list;
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                that.$hideLoading();
                uni.hideLoading();
                that.$event.on(that.$const.EVENT_USER_LOGIN).then(()=>{
                    that.getSetting();
                });
            });
        },
        getMore() {
            let that = this;
            uni.showLoading({
                mask: true,
                title: '加载中...'
            });
            that.$request({
                url: that.$api.bonus.detail,
                data: {
                    status: that.activeTab,
                    page: that.page
                },
            }).then(response=>{
                uni.hideLoading();
                if(response.code == 0) {
                    that.loading = null;
                    let more = response.data.list;
                    if (more.length > 0) {
                        if (that.list[that.list.length - 1].date == more[0].date) {
                            that.list[that.list.length - 1].list = that.list[that.list.length - 1].list.concat(more[0].list);
                            more.shift();
                            that.list = that.list.concat(more)
                        }else {
                            that.list = that.list.concat(more)
                        }
                        that.page++;
                    }
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(e => {
                uni.hideLoading();
            });
        },
        toGoods(id) {
            uni.navigateTo({
                url: '/pages/goods/goods?id=' + id
            });
        },
    },

    onLoad(options) { this.$commonLoad.onload(options);
        let that = this;
        that.$showLoading({
            type: 'global',
            text: '加载中...'
        });
        that.getList();
    },
    onReachBottom() {
        this.getMore();
    }
}
